/* General Styles */
body, html {
  margin: 0;
  padding: 0;
  font-family: 'Arial', sans-serif;
  background-color: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(10px);
  overflow-x: hidden;
}

.home-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

/* Resume Builder Section */
.resume-builder {
  position: absolute;
  top: 20px;
  left: 20px;
  padding: 15px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-radius: 10px;
  width: 250px;
}

.resume-builder h3 {
  margin-bottom: 15px;
  color: #ffffff;
}

.resume-builder input,
.resume-builder textarea {
  width: 100%;
  padding: 8px;
  margin-bottom: 10px;
  border-radius: 5px;
  border: none;
}

.resume-builder button {
  padding: 10px;
  background-color: #36a2eb;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.resume-builder button:hover {
  background-color: #2980b9;
}

/* Main Content Layout */
.main-content {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 150px;
  padding: 20px;
}

/* Left Section: Upcoming Job Opportunities */
.left-section {
  width: 200px;
  padding: 15px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-radius: 10px;
}

.left-section h3 {
  color: #ffffff;
}

.left-section ul {
  list-style-type: none;
  padding: 0;
}

.left-section li {
  margin-bottom: 10px;
  color: #ffffff;
}

/* Center Section: Company List */
.center-section {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.company-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 15px;
  width: 100%;
}

.company-card {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  padding: 15px;
  border-radius: 10px;
  text-align: center;
  transition: transform 0.3s ease-in-out;
}

.company-card:hover {
  transform: translateY(-10px);
}

.company-logo {
  max-width: 100px;
  max-height: 100px;
  margin-bottom: 10px;
}

.company-card button {
  padding: 10px;
  margin: 5px;
  background-color: #3498db;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.company-card button:hover {
  background-color: #2980b9;
}

/* Right Section: Internship Opportunities */
.right-section {
  width: 200px;
  padding: 15px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-radius: 10px;
}

.right-section h3 {
  color: #ffffff;
}

.right-section ul {
  list-style-type: none;
  padding: 0;
}

.right-section li {
  margin-bottom: 10px;
  color: #ffffff;
}

/* Alumni Section */
.alumni-section {
  margin-top: 50px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  padding: 20px;
  border-radius: 10px;
  width: 80%;
}

.alumni-section h3 {
  color: #ffffff;
}

/* Statistics Section */
.statistics-section {
  margin-top: 50px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  padding: 20px;
  border-radius: 10px;
  width: 80%;
}

.statistics-section h3 {
  color: #ffffff;
}

.pie-chart {
  max-width: 300px;
  margin: auto;
}

/* Fireflies Animation */
.fireflies {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: -1;
}

.firefly {
  position: absolute;
  background: rgba(255, 255, 255, 0.8);
  box-shadow: 0 0 4px rgba(255, 255, 255, 0.6);
  border-radius: 50%;
  animation: move 5s infinite ease-in-out;
}

@keyframes move {
  0%, 100% {
      transform: translateX(0) translateY(0);
  }
  50% {
      transform: translateX(100px) translateY(200px);
  }
}

/* Glassmorphism Card Hover Effects */
.card {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  padding: 20px;
  border-radius: 12px;
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}

.card:hover {
  transform: translateY(-10px) scale(1.05);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

/* Scrollable Sections */
.left-section, .right-section, .center-section {
  max-height: 700px;
  overflow-y: auto;
  scrollbar-width: thin;
}

.left-section::-webkit-scrollbar, .right-section::-webkit-scrollbar, .center-section::-webkit-scrollbar {
  width: 5px;
}

.left-section::-webkit-scrollbar-thumb, .right-section::-webkit-scrollbar-thumb, .center-section::-webkit-scrollbar-thumb {
  background-color: #3498db;
  border-radius: 10px;
}
